import React, { useState , useEffect , useRef  } from "react";
import myStyle from './Main.module.scss';
import BankAccount from './content/BankAccount';
import Report from './content/Report';
import Directions from './content/Directions';
import Gallery from './content/Gallery';

import axios from 'axios';

function Main({imageSrc , activeSection , setActiveSection , param}) {
	// const [activeSection, setActiveSection] = useState(1); //   "schedule" 
	  
  const [sender, setSender] = useState("");
	
  const [recipient, setRecipient] = useState(param=== "1"? "":""); // Default recipient
  const [message, setMessage] = useState(""); 
  const [phone, setPhone] = useState(""); 
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [ckBar, setCkBar] = useState(true); 
  const [ckSendMsg, setCkSendMsg] = useState(false); 
  // const [ckBar, setCkBar] = useState(true); 

  const handleChange = () => {
    setCkBar(!ckBar); //     
  };
  const handleRecipientChange = (e) => setRecipient(e.target.value);
  const handleSenderChange = (e) => setSender(e.target.value);
  const handlePhoneChange = (e) => setPhone(e.target.value);

	const handleSubmit = async () => {
    if (isSubmitting) return; //     
      setIsSubmitting(true); //    
			setCkBar(false);
    //  
    if (!sender.trim()) {
      alert("  !");
			setIsSubmitting(false); //    
      return;
    }
  
		// if (!phone.trim()) {
		// 	alert(" !");
		// 	return;
		// }
		
		if (phone.length < 10) {
			alert(" .");
			setIsSubmitting(false); //    
			return;
		}
  
    // Firebase Functions  URL
    const url = process.env.REACT_APP_MSG_URL;

    
    //  
    const data = {
      sender: sender.trim(),
      message: phone.trim(),
      recipient: recipient.trim(),
      msgType: "",
    };
  
    try {
      // POST 
      const response = await axios.post(url, data);
  
      //   
      if (response.status === 200) {
        alert(" .");
				setCkSendMsg(true);
        //   
        // setSender("");
        // setMessage("");
        //setRecipient(""); //   
      } else {
        // alert("   .");
      }
    } catch (error) {
      //  
      console.error("Error sending message:", error);
      // alert("  .  .");
    }finally{ setIsSubmitting(false); }

  };


  return (
		<div className={myStyle.main}>
		{activeSection!="1"?"": ckSendMsg?"":
		<div className={`${myStyle.container} ${"no-slide-area"}`}>
			{!ckBar?"":
			<div className={myStyle.barTitle}>    </div>
			}
			<div className={`${myStyle.ckBar} ${!ckBar? myStyle.ckbarSize:""} `}>
				{/* <div className={myStyle.barTitle}>    </div> */}
 
				<div className={myStyle.barBt} onClick={handleChange}>  
				{!ckBar?"":""}
				</div>

				{!ckBar?"":
				<div className={myStyle.bar}> 
					<form className={myStyle.form}>
						<div className={myStyle.group}>
							{/*   */}
							<div className={myStyle.inputGroup}>
								<label htmlFor="name"></label>
								<input type="text" id="name" name="name" 
								value={sender}
								onChange={handleSenderChange}
								placeholder="" />
								<label htmlFor="phone"><span style={{fontSize:"0.7em"}}>('-'    )</span></label>
								<input type="tel" id="phone" name="phone" 
								value={phone} onChange={handlePhoneChange}
								maxLength="11"
								//  
								onInput={(e) => {e.target.value = e.target.value.replace(/[^0-9]/g, "");}}
								placeholder="01012341234" />
							</div>
							<div>
									{param === "1" ? 
									<div style={{margin:"1em"}}>
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value=""
													checked={recipient === ""}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											
										</label>
										<br></br>
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value=""
													checked={recipient === ""}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											
										</label>	
									</div>
									
								: <div style={{margin:"1em"}}>
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value=""
													checked={recipient === ""}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											
										</label>
										<br></br>	
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value=""
													checked={recipient === ""}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											
										</label>	
									</div>
									}											
								</div>
							<div className={`${myStyle.inputGroup}`}>
								<div type="submit" className={`${myStyle.bt2}`} onClick={handleSubmit}></div>
							</div>
						</div>
						{/* /  */}
						{/*   */}
						{/* <div className={myStyle.inputGroup}>
							<button type="submit">   </button>
						</div> */}
					</form>
				</div>
			}
			</div>

		</div>

			}
			<div className={myStyle.title}>
				<div className={myStyle.buttonGroup}>
					<button className={`${myStyle.customButton} ${activeSection === 1 ? myStyle.selected : ""}`} onClick={() => setActiveSection(1)} > <span className={myStyle.point}></span></button>
					<button className={`${myStyle.customButton} ${activeSection === 2 ? myStyle.selected : ""}`} onClick={() => setActiveSection(2)}> <span className={myStyle.point}></span></button>
					<button className={`${myStyle.customButton} ${activeSection === 3 ? myStyle.selected : ""}`} onClick={() => setActiveSection(3)}> <span className={myStyle.point}></span></button>
				</div>
			</div>
			<div className={myStyle.content}>
				{/*    */}
				{activeSection === 1 && <Report imageSrc={imageSrc}/>}
				{activeSection === 2 && <Directions/>}
				{activeSection === 3 && <BankAccount 
				sender={sender} setSender={setSender} 
				recipient={recipient}  setRecipient={setRecipient} 
				message={message}  setMessage={setMessage} 
				param={param} 
				/>}
				{activeSection === 4 && <Gallery/>}
		
			</div>

    </div>

  );
}


export default Main;
