import { getStorage, ref, uploadBytesResumable, getDownloadURL ,  uploadBytes } from 'firebase/storage';
import { storage  , auth, db } from "../../firebase";

//   
const resizeImage = (file, maxSize = 100) => {
	return new Promise((resolve, reject) => {
		const img = new Image();
		const reader = new FileReader();
		console.log("resizeImage --");
		reader.onload = () => {
			img.onload = () => {
				const canvas = document.createElement("canvas");
				const ctx = canvas.getContext("2d");

				//      (100MB     )
				const maxWidth = 1024; //   ( 1024px )
				const maxHeight = 1024; //   ( 1024px )
				
				let width = img.width;
				let height = img.height;

				//     
				if (width > height) {
					if (width > maxWidth) {
						height = (height * maxWidth) / width;
						width = maxWidth;
					}
				} else {
					if (height > maxHeight) {
						width = (width * maxHeight) / height;
						height = maxHeight;
					}
				}

				canvas.width = width;
				canvas.height = height;

				//  
				ctx.drawImage(img, 0, 0, width, height);

				//    Blob  
				canvas.toBlob((blob) => {
					resolve(blob);
				}, "image/jpeg", 0.8); // 0.8   (0~1  )
			};

			img.src = reader.result;
		};

		reader.readAsDataURL(file);
	});
};

//   
const uploadImage = async (image) => {
	console.log("uploadImage In")
  if (!image) return null;
	console.log("uploadImage   ")

  const storageRef = ref(storage, `images/${image.name}`);  // 'images'  
  const uploadTask = uploadBytesResumable(storageRef, image);

  return new Promise((resolve, reject) => {
    uploadTask.on(
      'state_changed',
      null,
      (error) => reject(error), //  
      async () => {
        try {
          const downloadURL = await getDownloadURL(uploadTask.snapshot.ref);
          resolve(downloadURL); //  URL 
        } catch (error) {
          reject(error);
        } finally{
					console.log("uploadImage  ")
				}
      }
    );
  });
};
	

export { uploadImage, resizeImage  };
