import React, { useState , useEffect } from "react";
import kakaoIcon from "./icons/kakaomap_vertical_ko.png"; //   
import naverIcon from "./icons/Map_Service_Icon.png";
import tmapIcon from "./icons/tmap_icon.jpg";
import mapImg from 'assets/images/.jpg';

const MapLinks = ({ latitude, longitude, placeName }) => {
  const [isModalOpen, setIsodalOpen] = useState(false); //   
  const [isWideScreen, setIsWideScreen] = useState(false);
  //   URL 
  const kakaoUrl = `https://map.kakao.com/link/to/${placeName},${latitude},${longitude}`;
  const naverUrl = `nmap://navigation?dlat=${latitude}&dlng=${longitude}&dname=${placeName}`;
  const tmapUrl = `tmap://route?goalname=Location&goalx=${longitude}&goaly=${latitude}`;

  //  / 
  const toggleModal = () => {
    setIsodalOpen(!isModalOpen);
  };


  useEffect(() => {
    const handleResize = () => {
      setIsWideScreen(window.innerWidth / window.innerHeight > 1); //   
    };

    handleResize(); //  
    window.addEventListener('resize', handleResize); //   
    return () => window.removeEventListener('resize', handleResize); //  
  }, []);

  const styles = {
    container: {
      backgroundColor: '#f9f9f9',
      padding: '20px',
      borderRadius: '10px',
      fontFamily: 'Cafe24Shiningstar, sans-serif',
      boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
      maxWidth: '600px',
      margin: '20px auto',
      textAlign: 'left',
    },
    title: {
      textAlign: 'center',
      fontSize: '1.8rem',
      marginBottom: '1rem',
      color: '#4caf50',
    },
    section: {
      marginBottom: '1.5rem',
    },
    heading: {
      fontSize: '1.4rem',
      color: '#333',
      marginBottom: '0.5rem',
    },
    text: {
      fontSize: '1rem',
      lineHeight: '1.6',
      color: '#666',
    },
    highlight: {
      color: '#4caf50',
      fontWeight: 'bold',
    },
    bus: {
      color: '#af4c50',
      fontWeight: 'bold',
    },
  };

  return (
    <div style={{ display: "flex",
      flexDirection: 'column',
      height:"100%", justifyContent:"flex-start", gap:"1em", paddingTop:"0.5em"

    }}>
      {/* Kakao Map */}

        <a href={kakaoUrl} target="_blank" rel="noopener noreferrer">
          <img
            src={kakaoIcon}
            alt="Kakao Map"
            style={{ width: "50px", height: "auto", cursor: "pointer" }}
          />
          
        </a>

      {/* Naver Map */}
      <a href={naverUrl} target="_blank" rel="noopener noreferrer">
        <img
          src={naverIcon}
          alt="Naver Map"
          style={{ width: "50px", height: "auto", cursor: "pointer" }}
        />
      </a>
      

      {/* TMap */}
      <a href={tmapUrl} target="_blank" rel="noopener noreferrer">
        <img
          src={tmapIcon}
          alt="TMap"
          style={{ width: "50px", height: "auto", cursor: "pointer" }}
        />
      </a>

      <button
        onClick={toggleModal}
        style={{
          width: "50px", height: "50px",
          fontSize: " clamp(15px, 1em, 40px)",
          cursor: "pointer",
          color:"rgba(0,0,0,0.9)",
          borderRadius:"3px",
          marginBottom: "1em",
          border: "1px solid rgba(145,136,57,0.3)", // border  box-shadow 
          backgroundColor: "rgba(145,136,57,0)",
        }}
      >
        
        <br/>
      </button>

      {isModalOpen && (
        <div
          onClick={toggleModal} 
          style={{
            position: "fixed",
            top: 0, left: 0,
            width: "100%", height: "100%",
            justifyContent: "center", alignItems: "center",
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            display: "flex",
            zIndex: 1000,
          }}
        >
          <div
            onClick={(e) => e.stopPropagation()} //    
            className="no-slide-area"
            style={{
              backgroundColor: "white",
              padding: "20px",
              borderRadius: "10px",
              width: "90%",
              fontSize:"1.3em",
              textAlign: "left",
              position: "relative",
            }}
          >

            {/*    */}
            <button
              onClick={toggleModal}
              style={{
                position: "absolute",
                top: "10px",right: "10px",
                backgroundColor:"white",
                border: "none",
                fontSize: "18px",
                cursor: "pointer",
              }}></button> 

      <h2 style={styles.title}>  </h2>
        <div style={styles.section}>
          <h3 style={styles.heading}>    (15 )</h3>
          <p style={styles.text}>
            <span style={styles.highlight}> </span>    
          </p>
        </div>
        <div style={styles.section}>
          <h3 style={styles.heading}> [  ]</h3>
          <p style={styles.text}>
            <span style={styles.highlight}>503  </span> : 5    <span style={styles.bus}></span>  
          </p>
        </div>
        <div style={styles.section}>
          <h3 style={styles.heading}> [  ]</h3>
          <p style={styles.text}>
            <span style={styles.highlight}>815  </span> : <span style={styles.bus}></span>         
          </p>
        </div>


          <img
            src={mapImg}
            style={{
              width: isWideScreen ? 'auto' : '100%',
              height: isWideScreen ? '100%' : 'auto',
              objectFit: 'cover',
              position: 'relative', //   

            }}
            alt="Example"
          />

            {/*   */}
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                gap: "1em",
              }}
            >
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MapLinks;
