import React, { useState, useEffect } from "react";
import './App.css';
import { HelmetProvider , Helmet } from "react-helmet-async";
import { Router, Route, Routes } from "react-router-dom";
import { useSwipeable } from "react-swipeable";

import DynamicMeta from "./components/DynamicMeta";
import Haeder from './components/Haeder'
import Main from './components/Main'
import Footer from './components/Footer'
import StartPage from './components/StartPage'
import MenuPage from './components/MenuPage'



import image2 from './assets/images/_2.jpg';
import bkImgIn from './assets/images/.jpg';
import bkImgOut from './assets/images/_1.jpg';


function App() {
  const [loading, setLoading] = useState(true);
  const [loading2, setLoading2] = useState(true);
  const [activeSection, setActiveSection] = useState(1);
  const [menuPage, setMenuPage] = useState(false);
  const [param, setParam] = useState(null);

  useEffect(() => {
    // URLSearchParams   
    const searchParams = new URLSearchParams(window.location.search);
    const paramValue = searchParams.get("param");
    setParam(paramValue || "1");

  }, []); //       
  
  const pageCnt = 4;

  //      
  const handleAction = (direction) => {
    switch (direction) {
      case "Left":
        //console.log("   ");
        //   
          if(activeSection === pageCnt)  setActiveSection(1);
          else  setActiveSection(activeSection + 1);
        break;
      case "Right":
        // console.log("   ");
        //   
        if(activeSection === 1)   setActiveSection(pageCnt);
        else  setActiveSection(activeSection - 1);
        break;
      case "Down":
        // console.log("   ");
        if(activeSection === pageCnt)  setActiveSection(1);
        else  setActiveSection(activeSection + 1);
        // setMenuPage(false);
        //   
        break;
      case "Up":
        // console.log("   ");
        if(activeSection === 1)   setActiveSection(pageCnt);
        else  setActiveSection(activeSection - 1);
        // if(menuPage)
        //   if(activeSection === 1)   setActiveSection(3);
        //   else  setActiveSection(activeSection - 1);
        // setMenuPage(true);

        //   
        break;
      default:
        console.log(" ");
    }
  };
  const isZooming = () => {
    //   /  
    return window.visualViewport?.scale !== 1;
  };


  //   
  const handlers = useSwipeable({
    onSwipedLeft: (eventData) => {
      console.log("scale");
      console.log(eventData );
      if (!isZooming() && !eventData.event.target.closest(".no-slide-area")) {
        handleAction("Left");
      }
    },
    onSwipedRight: (eventData) => {
      if (!isZooming() && !eventData.event.target.closest(".no-slide-area")) {
        handleAction("Right");
      }
    },
    onSwipedUp: (eventData) => {
      if (!isZooming() && !eventData.event.target.closest(".no-slide-area")) {
        handleAction("Up");
      }
    },
    onSwipedDown: (eventData) => {
      if (!isZooming() && !eventData.event.target.closest(".no-slide-area")) {
        handleAction("Down");
      }
    },
  });

  useEffect(() => {
    const img = new Image();
    img.src = image2;
  }, []);

  useEffect(() => {
    //      
    const timer = setTimeout(() => {
      setLoading2(false);
    }, 2000); // : 5    
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    //      
    const timer = setTimeout(() => {
      setLoading(false);
    }, 7000); // : 5    
    return () => clearTimeout(timer);
  }, []);

  const handleStartClick = () => {
    setLoading(false); //     
  };

  return (
    <div className="App">

      {/* {param} */}
      {loading ?
        (<StartPage onStartClick={handleStartClick} bkImgIn={bkImgIn} bkImgOut={bkImgOut}  param={param}/> ):
        ("")}
      {!loading2 ?
      (<div className="container"  {...handlers}> 
      <div
        className="backgroundOverlay"
        style={{
          backgroundImage: `url(${bkImgIn})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          maskImage:
            "linear-gradient(to top, rgba(150, 255, 150, 0.5) 25%, rgba(255, 255, 255, 0.5) 60%)",
          WebkitMaskImage:
            "linear-gradient(to bottom, rgba(150, 255, 150, 0.1) 25%, rgba(255, 255, 255, 0.1) 30%)",
        }}
      ></div>
        {/*   */}
        <div className="haeder"> 
          <Haeder/>
        </div>
        {/*    */}
        <div className="main"> 
          {menuPage ?
          (<MenuPage setMenuPage={setMenuPage} activeSection={activeSection} setActiveSection={setActiveSection}/> ):
          <Main imageSrc={image2} activeSection={activeSection} setActiveSection={setActiveSection} param={param}/>}
        </div>
        {/*   */}
        <div className="footer"> 
          <Footer param={param}/>
        </div>
      </div>):("")}
    </div>
  
  );
}

export default App;
